.App{
    border: 5px solid yellow;
    display: flex;
    /* align-items: center; */
    justify-content: center;
    
    min-height: 680px;
    padding-top: 0px;
    background-color: rgb(182, 184, 175);

}
.addbox{
    width: 25%;
    text-align: center;
    margin-top: 0px;
    background-color: rgb(255, 255, 255);
}
.li{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    margin-bottom: 1px;
    background-color: rgb(244, 235, 221);
}
.li>button{
    background-color: red;
    border: 1px solid red;
    border-radius: 2px;
    
}